.map {
  margin: 0;
  padding: 0;
}

.tour-title h1 {
  color: black;
  padding: 2rem 0;
  text-align: center;
  position: relative;
}

.tour-title h1::before {
  content: "";
  position: absolute;
  bottom: 15px;
  right: 0;
  left: 0;
  border: 2px solid #000;
  width: 4rem;
  margin: 0 auto;
}
