.tour-carts {
  max-width: 40rem;
  margin: 0 auto;
}

@media (max-width: 660px) {
  .tour-carts {
    max-width: 95%;
  }
}

.cart {
  background-color: white;
  margin: 2rem 0;
  padding-bottom: 1rem;
}

.cart img {
  width: 100%;
  height: 20rem;
  object-fit: cover;
}

.info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 1rem;
}

.text {
  padding: 0.5rem 1rem;
  text-align: justify;
}

.tour-btn {
  text-align: center;
}

.tour-btn button {
  color: black;
  border: 2px solid rgb(83, 134, 246);
  text-align: center;
  padding: 0.3rem 1rem;
  font-size: 0.8rem;
}
