.reviews {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin: 1rem 0;
}

.review {
  width: 20rem;
  background-color: white;
  text-align: center;
  padding: 1rem 0;
}

.reviews img {
  width: 6rem;
  height: 6rem;
  object-fit: cover;
  border-radius: 50%;
}

.review h3 {
  color: black;
}

.review p {
  padding: 1rem;
}

.review-title h1 {
  padding-bottom: 1rem;
}

.group .icon {
  color: black;
}

.btn button {
  color: black;
  border: 1px solid #000;
  font-size: 0.9rem;
}
