.navigation {
  height: 100px;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  padding: 0 3rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition: all 400ms;
}

.active {
  background-color: rgb(0, 0, 0);
}

nav span a {
  margin-right: 2rem;
  font-size: 1.2rem;
  cursor: pointer;
  text-decoration: none;
  color: rgb(255, 250, 250);
}

button {
  padding: 0.5rem 1.5rem;
  font-size: 1rem;
  cursor: pointer;
  background-color: transparent;
  border: 2px solid rgb(255, 255, 255);
  color: white;
}

h2 {
  color: white;
  font-size: 1.7rem;
}
